define('skylark-langx/_attach',[],function(){
    return  function attach(obj1,path,obj2) {
        if (typeof path == "string") {
            path = path.split(".");//[path]
        };
        var length = path.length,
            ns=obj1,
            i=0,
            name = path[i++];

        while (i < length) {
            ns = ns[name] = ns[name] || {};
            name = path[i++];
        }

        return ns[name] = obj2;
    }
});
define('skylark-langx/skylark',[
    "./_attach"
], function(_attach) {
    var skylark = {
    	attach : function(path,obj) {
    		return _attach(skylark,path,obj);
    	}
    };
    return skylark;
});

define('skylark-ajaxfy-router/routers',[
	"skylark-langx/skylark"
],function(skylark){

	return skylark.attach("ajaxfy.routers",{});	
});

define('skylark-ajaxfy-router/main',[
    "./routers"
], function(routers) {
    return routers;
});

define('skylark-ajaxfy-router', ['skylark-ajaxfy-router/main'], function (main) { return main; });

